<template>
  <div>
    <!-- 头部 -->
    <header class="fl-ex al-it ju-ar">
      <i class="el-icon-arrow-left" @click="$router.push('/dynamic')"></i>
      <div class="box fl-ex al-it ju-fe">
        <i class="el-icon-search"></i>
        <i class="el-icon-more"></i>
      </div>
    </header>
    <!-- 主体 -->
    <div class="main" v-for="(item,index) in list" :key="index">
      <div class="h1">
        <h1>{{item.h1}}</h1>
      </div>
      <div class="p1">
        <p>{{item.p1}}</p>
      </div>
      <div class="img">
        <img src="../../../static/img/10.png" alt="图片" />
      </div>
      <div class="p1">
        <p>
          {{item.p2}}
          <br />
          {{item.p3}}
        </p>
      </div>
      <footer class="fl-ex al-it">
        <p style="margin-left:38%;">阅读</p>
        <p style="margin-left:2%;">|</p>
        <i class="el-icon-view"></i>
        <span>{{item.num}}</span>
      </footer>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [
        {
          h1: "省总工会召开“不忘初心,牢记使命”主题教育第二次专题研讨会",
          p1:
            "这两天，省总工会召开“不忘初心、牢记使命”主题教育第二次专题研讨会，以 “ 解放思想、改革创新、锐意进取、当好新时代答卷人 ” 为主题，围绕习近平总书记在中央政治局第十五次集体学习时重要讲话精神和工会干部 “ 忠诚党的事业、服务职工群众 ” 使命担当开展研讨，省总机关、省总干校全体党员和省康复医院副处以上党员干部参加，省委第八巡回指导组莅临指导，省总党组成员、副主席吴有祯主持会议。",
          p2:
            "研讨会未预设指定发言对象，大家积极发言、气氛热烈。省总班子成员、部室负责人和普通党员共12名同志结合学习习总书记重要讲话精神和前期开展调研发现的问题做了专题发言。大家在讨论中一致认为，习近平总书记在中央政治局集体学习时的重要讲话精神为深入推进主题教育，加强新时代党的建设提供了基本遵循。要进一步深入学习领会，抓好贯彻落实，刀刃向内检视问题，真刀真枪整改落实，着力推动工会工作迈上新台阶；要进一步解放思想、深化改革、锐意进取，与时俱进履行 “ 维护职工合法权益，竭诚服务职工群众 ” 的基本职责，永葆党员朝气，坚持自我革命，紧随新时代，紧贴职工心，聚焦突出问题，构建服务体系，创新载体平台，优化维权服务，当好维权服务的“答卷人”；要把竭诚为职工群众服务作为工会一切工作的出发点和落脚点，全心全意为广大职工群众服务，认真倾听职工群众呼声，维护好广大职工群众包括农民工合法权益，扎扎实实为职工群众做好事、办实事、解难事。",
          p3:
            "省委第八巡回指导组组长李勇点评时指出，省总工会开展“不忘初心、牢记使命”主题教育第二次专题研讨，能够紧密结合我省工作实际，按照 “五四战略”目标要求，创新性地确定了专题研讨主题，发言凸显工作特色，突出点面结合，体现了省总班子成员、中层干部和一般党员的积极性和踊跃性，达到了共同学习、共同提高的效果。一是思想认识进一步提高。全体党员在工作中学习，学习中工作，紧密联系个人思想和工作实际，谈认识、找差距、理思路，进一步增强了守初心、担使命的责任感和使命感。二是调查研究深入扎实。班子成员带队领题，结合省总工作职责任务，围绕工会职工和产业工人关切，查找症结、分析原因、解决问题，进一步推动了调研工作往深里走、往实里走。三是坚持刀刃向内检视问题。坚持把检视问题贯穿主题教育始终，自觉对照党章、对照先进典型，突出自我检视，多形式、多渠道地听取意见，自我解剖找准根源，制定问题清单，进一步推动整改落实。四是专题研讨交流踊跃热烈。党员干部高度结合主责主业、结合自身思想实际、结合调查研究、结合主题实践活动，谈学习、谈感受、谈认识，交流踊跃，发言积极。李勇组长还就进一步抓好专项整治、检视问题、整改落实提出了要求。吴有祯副主席在总结讲话中强调，要以专题研讨为契机，在学习教育上再深化、调查研究上再深化、检视问题上再深化、整改落实上再深化，着力推动主题教育扎实开展。",
          num: "2230"
        }
      ]
    };
  }
};
</script>

<style scoped>
header {
  width: 100%;
  height: 3rem;
}
header .box {
  width: 85%;
  height: 3rem;
}
header i {
  font-size: 1.4rem;
  color: black;
}
/* 主题 */
.main {
  width: 100%;
}
.main .h1 {
  width: 90%;
  margin-left: 0.5rem;
}
.main h1 {
  font-size: 1.35rem;
  color: black;
  text-align: left;
  line-height: 1.8rem;
  /* font-weight: bold; */
  letter-spacing: 0.15rem;
}
.main .p1 {
  width: 95%;
  margin: 0 auto;
}
.main .p1 p {
  text-align: left;
  font-size: 1.02rem;
  color: black;
  margin-top: 1rem;
  line-height: 1.45rem;
  letter-spacing: 0.12rem;
}
.main .img {
  width: 95%;
  height: 12rem;
  margin: 0 auto;
  margin-top: 1rem;
}
.main .img img {
  width: 100%;
  height: 12rem;
}
.main footer {
  width: 100%;
  height: 3rem;
}
.main footer p{
  font-size:0.9rem;
  color:#b0b0b0;
}
.main footer i{
   margin-left:2%; 
   color:#b0b0b0;
}
.main footer span{
   font-size:0.9rem;
   color:#b0b0b0;
}
</style>


